import { I3DProduct } from '@designhubz-widget/api';
import { _Widget } from '../widgets/_Widget';
import { _EditorWidget } from './_EditorWidget';
import { _3DProduct } from '../products/_3DProduct';


export namespace Internal
{
    export const EditorWidget = _EditorWidget;

    export async function createEditorWidget<T extends Record<string, string>>(defaultVariation: I3DProduct<T>, domElement: HTMLDivElement)
    {
        const widget = new EditorWidget<T, I3DProduct<T>>(defaultVariation as any, domElement);
        return await widget.waitForInit;
    }
}