import { I3DProduct } from '@designhubz-widget/api';
import { Data } from '@designhubz-widget/data';
import { _Widget } from '../widgets/_Widget';
import { _Product } from '../products/_Product';
import { _3DProduct } from '../products/_3DProduct';
import { _3DWidget } from '../widgets/_3DWidget';
import * as Messaging from '../widgets/Messaging';

export class _EditorWidget<T, P extends I3DProduct<T>> extends _3DWidget<T, _3DProduct<T>>
{
    public _editor = true;

    constructor(
        defaultVariation: P,
        domElement: HTMLDivElement
    ){
        super(defaultVariation as any, domElement, `${Data.apps.editor}?workspaceId=${defaultVariation.workspaceKey}&referenceId=${defaultVariation.productKey}`);
    }

    public async debug()
    {
        const query = await this.QueryAsync({
            event: Messaging.WidgetEvent.Debug,
            value: null
        });
        return Boolean(query.value);
    }

    public async save()
    {
        const query = await this.QueryAsync({
            event: Messaging.WidgetEvent.Save,
            value: null
        });
        return Boolean(query.value);
    }
}
