import { I3DProduct } from '@designhubz-widget/api';
import { _Product } from './_Product';
import { _3DWidget } from '../widgets/_3DWidget';

export class _3DProduct<T> extends _Product<T> implements I3DProduct<T>
{
    public _3D = true;
    public readonly is3D = () => true;
    
    public async create3DWidget(domElement: HTMLDivElement): Promise<_3DWidget<T, this>>
    {
        const widget = new _3DWidget(this, domElement);
        return await widget.waitForInit;
    }
}
