import { IEyewearProduct } from '@designhubz-widget/api';
import { _3DProduct } from './_3DProduct';
import { _3DWidget } from '../widgets/_3DWidget';
import { _EyewearWidget } from '../widgets/_EyewearWidget';

export class _EyewearProduct<T> extends _3DProduct<T> implements IEyewearProduct<T>
{
    public _eyewear = true;
    public _3D = true;
    public readonly isEyewear = () => true;
    
    public async create3DWidget(domElement: HTMLDivElement): Promise<_3DWidget<T, this>>
    {
        const widget = new _3DWidget(this, domElement);
        return await widget.waitForInit;
    }

    public async createEyewearWidget(domElement: HTMLDivElement): Promise<_EyewearWidget<T, this>>
    {
        const widget = new _EyewearWidget(this, domElement);
        return await widget.waitForInit;
    }

    public async createMultiWidget(domElement: HTMLDivElement): Promise<any>
    {
        throw new Error('Method not implemented.');
    }
}
