import { Dev } from '@designhubz-widget/api';
import { GetUrlParam } from '@designhubz-widget/utils';

/** enabled = ?designhubz_debug=bool */
export const DEV_DEBUG_URL_PARAM = 'designhubz_debug';

const formatOptions = ['font-weight', 'color', 'background'];

export const _dev = new (
    class _Dev implements Dev
    {
        public debug = false;

        public Log(...args: Array<any>)
        {
            if(this.debug)
            {
                const formatting: string[] = [];
                for(let i = 0; i < args.length - 1; i++)
                {
                    const arg = args[i];
                    const text = args[i + 1];
                    if(typeof arg === 'string' && typeof text === 'string' && arg.slice(0, 2) === '#(')
                    {
                        const optionsArgs = arg.slice(2, arg.length - 1).split(',');
                        let format = formatOptions.map( (o, i) => 
                            typeof optionsArgs[i] === 'string' && optionsArgs[i].length > 0
                                ? `${o}:${optionsArgs[i]};`
                            : ''
                        ).join(' ');
                        
                        args.splice(i, 1);
                        args[i] = `%c ${text} `;
                        formatting.push(format);
                    }
                }
                if(formatting.length > 0) console.log(args.map( arg => arg.toString() ).join(''), ...formatting);
                else console.log(...args);
            }
        }

        public CreateError(name: string, message?: string)
        {
            this.Log(`Created ERROR '${name}': '${message}'`);
            const error = new Error(message);
            error.name = name;
            return error;
        }
    }
);

const bool = (p?: string) => p === '1' || p === 'true';
if(bool(GetUrlParam(DEV_DEBUG_URL_PARAM))) _dev.debug = true;