import { IProduct, IScoredRecommendation } from '@designhubz-widget/api';

export abstract class _Product<T> implements IProduct<T>
{
    public readonly is3D = () => false;
    public readonly isEyewear = () => false;
    public readonly isMakeup = () => false;

    public readonly variations: this[];

    constructor(
        public readonly workspaceKey: string,
        public readonly productKey: string,
        public readonly properties: T,
        variations: _Product<T>[]
    )
    {
        this.variations = variations as this[];
    }

    public get thumbnailURL(){ return ''; }

    public fetchRecommendations(num: number, external?: IScoredRecommendation[]): Promise<IScoredRecommendation[]>
    {
        const recommendations = new Array<IScoredRecommendation>(num);
        recommendations.map( (_, i) => { 
            return {
                sku: i.toString(),
                score: i / num
            };
        });
        return new Promise( r => r(recommendations) );
    }
}
