import { IEyewearWidget, TrackingStatus } from '@designhubz-widget/api';
import { ISignal, Signal } from '@designhubz-widget/utils';
import { _Widget } from './_Widget';
import { _EyewearProduct } from '../products/_EyewearProduct';
import { Data } from '@designhubz-widget/data';

export class _EyewearWidget<T, P extends _EyewearProduct<T>> extends _Widget<T, P> implements IEyewearWidget<T>
{
    public _eyewear = true;
    
    public readonly onTrackingStatusChange: ISignal<TrackingStatus> = new Signal();
    
    constructor(
        defaultVariation: P,
        domElement: HTMLDivElement
    ){
        super(defaultVariation, domElement);

        this._iframe.setAttribute('allow', 'camera;microphone');
        this._iframe.src = `${Data.apps.viewer}?workspaceId=${defaultVariation.workspaceKey}&referenceId=${defaultVariation.productKey}`;
        this.Attach();
    }

    public get fit(){ return true; }
}