import { IProduct } from '@designhubz-widget/api';
import { Data } from '@designhubz-widget/data';
import { _Product } from './products/_Product';
import { ProductFromCache, ProductFromData } from './products/Factory';
import { _dev } from './_Dev';

export { Stat, TrackingStatus } from '@designhubz-widget/api';

export const version: string = require('../package.json').version;

export const dev = _dev;

export async function fetchProduct<T extends Record<string, string>>(workspaceKey: string, productKey: string, props?: T): Promise<IProduct<T>>
{
    if(typeof workspaceKey !== 'string' || typeof productKey !== 'string')
        throw new RangeError(`Invalid arguments: fetchProduct(${workspaceKey}: string, ${productKey}: string)`);
    let product = ProductFromCache<T>(workspaceKey, productKey);
    if(product === undefined)
    {
        _dev.Log('#(bold,#f00)', 'fetching product:', '#()', `(${workspaceKey}, ${productKey})`);
        const productData = (await Data.FetchProduct(workspaceKey, productKey)).productData;
        product = ProductFromData<T>(productData, props);
    }
    else
    {
        _dev.Log('#(bold,#f88)', '(cache) fetching product:', '#()', `(${workspaceKey} | ${productKey})`);
    }
    return product;
}

export async function fetchProducts<T extends Record<string, string>>(workspaceKey: string, productKeys: string[], props?: T): Promise<Record<string, IProduct<T> | undefined>>
{
    if(typeof workspaceKey !== 'string' || (productKeys instanceof Array) === false || productKeys.length === 0 || typeof productKeys[0] !== 'string')
        throw new RangeError(`Invalid arguments: fetchProducts(${workspaceKey}: string, ${productKeys.join(', ')}: string[])`);
    
    _dev.Log('#(bold,#000)', 'fetching products:', '#()', `(${workspaceKey}, [${productKeys.join(', ')}])`);
    const result: Record<string, IProduct<T> | undefined> = {};
    const stats = {cache: 0, fetch: 0, missing: productKeys.length};
    const productsToFetch: string[] = [];
    productKeys.forEach( productKey => {
        const product = ProductFromCache<T>(workspaceKey, productKey);
        if(product === undefined)
        {
            productsToFetch.push(productKey);
        }
        else
        {
            stats.cache++;
            stats.missing--;
            result[productKey] = product;
        }
    });
    if(productsToFetch.length > 0)
    {
        const productsMap = (await Data.FetchProducts(workspaceKey, productsToFetch));
        productsToFetch.forEach( productKey => {
            const product = productsMap[productKey];
            if(product !== undefined)
            {
                stats.fetch++;
                stats.missing--;
                result[productKey] = ProductFromData<T>(product.productData, props);
            }
        });
    }
    _dev.Log(
        '#(,#f88)', `${stats.cache} from cache`, ' | ', 
        stats.fetch > 0 ? '#(bold,#f00)' : '#(bold)', `${stats.fetch} fetched`, ' | ', 
        stats.missing > 0 ? '#(#fff,#f00)' : '#(bold)', `${stats.missing} missing`);
    return result;
}
