import { IWidget, IProduct, Stat } from '@designhubz-widget/api';
import { Signal, Dom, ISignal } from '@designhubz-widget/utils';
import { _dev } from '../_Dev';
import { _Product } from '../products/_Product';
import { Snapshot } from './Snapshot';
import * as Messaging from './Messaging';

export abstract class _Widget<T, P extends IProduct<T>> implements IWidget<T, P>
{
    public readonly waitForInit = new Promise<this>( resolve => (this as any)._waitForInit = resolve );

    protected _variation: P;
    protected _iframe: HTMLIFrameElement;

    private readonly _id = Math.floor(Math.random() * 1000) + '-';
    private _queriesCount = 0;
    private _pending = new Map<string, (arg: any) => void>();

    constructor(
        defaultVariation: P,
        public readonly domElement: HTMLDivElement
    ){
        onmessage = message => {
            if(message.data?.type === Messaging.DESIGNHUBZ_API_TYPE )
                this.OnWidgetMessage((message as Messaging.TWidgetMessage).data);
        };

        this._variation = defaultVariation;

        this._iframe = Dom.BuildHTML<HTMLIFrameElement>(`<iframe
    style="width: 1px; min-width: 100%; *width: 100%; height: 1px; min-height: 100%; *height: 100%;" 
    width="100%" height="100%" scrolling="no" frameBorder="0"
</iframe>`);
    }

    public get variation(){ return this._variation; }

    public async loadVariation(variation: P)
    {
        if(this._variation.variations.every( v => v.productKey !== variation.productKey ))
            throw new Error(`Product '${variation.productKey}' is not a variation of ${this._variation.variations.map( v => v.productKey ).join(', ')}`);
    }

    public async takeSnapshotAsync()
    {
        const query = await this.QueryAsync({
            event: Messaging.WidgetEvent.SnapshotRequest,
            value: {
                flip: false
            }
        });
        const imageData = new ImageData(new Uint8ClampedArray(query.value.data.buffer), query.value.width, query.value.height)
        return new Snapshot(imageData);
    }

    public sendStat(stat: Stat)
    {
        return Promise.resolve();
    }

    public async disposeAsync()
    {
        if(this._iframe.parentElement !== null) this._iframe.parentElement.removeChild(this._iframe);
    }

    protected OnWidgetMessage(data: Messaging.IWidgetMessageData)
    {
        let resolver = this._pending.get(data.id);
        if(resolver !== undefined)
        {
            resolver(data);
        }
        else
        {
            switch(data.event)
            {
                case Messaging.WidgetEvent.Init:
                    (this as any)._waitForInit(this);
                break;

                default:
                    _dev.Log(`No listeners on '${event}'`, data.value);
                break;
            } 
        }
    }

    protected async QueryAsync(message: Messaging.IWidgetMessageData, waitForReply?: boolean): Promise<Messaging.IWidgetMessageData>
    protected async QueryAsync(message: Omit<Messaging.IWidgetMessageData, 'type' | 'id'>, waitForReply?: boolean): Promise<Messaging.IWidgetMessageData>
    protected async QueryAsync(message: Messaging.IWidgetMessageData, waitForReply = true): Promise<Messaging.IWidgetMessageData>
    {
        if(this._iframe.contentWindow === null) throw new Error(`Can't query iframe's contentWindow.`);

        if(('type' in message && 'id' in message) === false)
        {
            message = Object.assign({
                type: Messaging.DESIGNHUBZ_API_TYPE,
                id: this._id + (this._queriesCount++)
            }, message);
        }

        const promise = waitForReply
            ? new Promise<Messaging.IWidgetMessageData>( r => this._pending.set(message.id, r) )
        : Promise.resolve(message);

        this._iframe.contentWindow!.postMessage(message, '*');
        return promise;
    }

    protected Attach()
    {
        // what kind of dom events do I need to handle?
        this.domElement.appendChild(this._iframe);
    }
}