import { 
    ISkinData, ISceneData, IProductData, 
    IDataSource, 
    IProductResponse, ISceneResponse, ISkinResponse, 
} from '@designhubz-widget/data';

const PRODUCT_DATA: IProductData[] = [];

const createProductData = (workspaceKey: string, productKey: string) => {
    let pd = PRODUCT_DATA.filter( d => d.productKey === productKey )[0];
    
    if(pd === undefined)
    {
        pd = {
            workspaceKey: workspaceKey,
            productKey: productKey,
            productType: 'Eyewear',
            properties: {colorCode: productKey, sizeCode: productKey, thumbnailURL: productKey},
            variations: PRODUCT_DATA
        };
        PRODUCT_DATA.push(pd);
    }

    return pd;
};

const SCENE_DATA: ISceneData = {
    descriptorURL: 'descriptors/scene.json'
};

const SKIN_DATA: ISkinData = {
    descriptorURL: 'descriptors/skin.json'
};

const responseTime = async () => await new Promise( r => setTimeout(r, 250) );

export class MockData implements IDataSource
{
    public async GetSkinResponse(workspaceKey: string): Promise<ISkinResponse>
    { 
        await responseTime();
        return {
            status: 'success',
            data: [{
                publicFolderUrl: 'skins/',
                workspaceId: workspaceKey,
                skinData: SKIN_DATA
            }]
        };
    }

    public async GetSceneResponse(workspaceKey: string, productKey: string): Promise<ISceneResponse>
    { 
        await responseTime();
        return {
            status: 'success',
            data: [{
                referenceId: productKey,
                workspaceId: workspaceKey,
                name: `Product(${productKey})`,
                publicFolderUrl: `https://${workspaceKey}.com`,
                sceneData: SCENE_DATA
            }]
        };
    }

    public async FetchProductResponse(workspaceKey: string, productKey: string): Promise<IProductResponse>
    {
        await responseTime();
        return {
            status: 'success',
            data: [{
                productData: createProductData(workspaceKey, productKey)
            }]
        };
    }

    public async FetchProductsResponse(workspaceKey: string, productKeys: string[]): Promise<IProductResponse>
    {
        await responseTime();
        return {
            status: 'success',
            data: productKeys.map( key => {
                return {
                    productData: createProductData(workspaceKey, key)
                };
            })
        };
    }
}