import { 
    ISkinData, ISceneData, IProductData, 
    IDataSource, 
    IProductResponse, ISceneResponse, ISkinResponse 
} from '@designhubz-widget/data';

const fetchJSON = async (url: string) => {
    const response = await fetch(url, { method: 'GET' });
    if(response.ok) return response.json();
}

// https://designhubz-viewer-data.s3-eu-west-1.amazonaws.com/user/workspace/eyewear/glass1/scene.json
const folder = {
    w: (root: string, workspaceKey: string) => `${root}user/workspace/${workspaceKey}/`,
    s: (root: string, workspaceKey: string, productKey: string) => `${folder.w(root, workspaceKey)}scene/${productKey}/`
};

export class TempData implements IDataSource
{
    public constructor(public readonly root: string)
    {}

    public Resolve(url: string)
    {
        return this.root + url;
    }

    public async GetSkinResponse(workspaceKey: string): Promise<ISkinResponse>
    { 
        const dirURL = folder.w(this.root, workspaceKey);
        const data = await fetchJSON(dirURL + 'skin.json') as ISkinResponse['data'][0];
        if(data !== undefined) data.publicFolderUrl = dirURL;
        const response: ISkinResponse = {
            status: data !== undefined ? 'success' : 'error',
            data: [data]
        };
        return response;
    }

    public async GetSceneResponse(workspaceKey: string, productKey: string): Promise<ISceneResponse>
    {
        const dirURL = folder.s(this.root, workspaceKey, productKey);
        const data = await fetchJSON(dirURL + 'scene.json') as ISceneResponse['data'][0];
        if(data !== undefined) data.publicFolderUrl = dirURL;
        const response: ISceneResponse = {
            status: data !== undefined ? 'success' : 'error',
            data: [data]
        };
        return response;
    }

    public async FetchProductResponse(workspaceKey: string, productKey: string, collectVariationDetails: boolean): Promise<IProductResponse>
    {
        const sceneEntry = await this.GetSceneResponse(workspaceKey, productKey);
        if(sceneEntry.status !== 'success' || sceneEntry.data[0] === undefined)
            throw new Error(`Error FetchProductResponse(${workspaceKey}, ${productKey})`);
        
        const scene = sceneEntry.data[0];
        const data: IProductResponse['data'][0] = {
            productData: scene.sceneData.productData
        };

        const productData = data.productData as any;
        if(productData.variations === undefined) productData.variations = [];
        if(collectVariationDetails)
        {
            if(productData.variations.length > 0 && typeof productData.variations[0] === 'string')
            {
                productData.variations = await Promise.all(
                    (productData.variations as string[]).map( async v => {
                        const vResponse = await this.FetchProductResponse(workspaceKey, v, false);
                        if(vResponse.status === 'success' && vResponse.data[0] !== undefined)
                        {
                            return vResponse.data[0];
                        }
                        else
                        {
                            return v;
                        }
                    })
                );
            }
        }
        
        const response: IProductResponse = {
            status: data !== undefined ? 'success' : 'error',
            data: [data]
        };
        return response;
    }

    public async FetchProductsResponse(workspaceKey: string, productKeys: string[], collectVariationDetails: boolean): Promise<IProductResponse>
    {
        throw 'FetchProductsResponse Not implemented';
    }
}