import { Data } from '@designhubz-widget/data';
import { TempData } from '@designhubz-widget/data-source';

const searchParams = (new URL(location.href)).searchParams;
const local = Boolean(searchParams.get('local')) ? 'https://localhost' : false;
const s3 = 'https://designhubz-viewer-data.s3-eu-west-1.amazonaws.com/';

const localFlag = local ? '?local=1' : '';
Data.apps.viewer = (local ? local + ':2020/example/viewer.html' : s3 + 'apps/viewer/index.html') + localFlag;
Data.apps.editor = (local ? local + ':2020/example/editor.html' : s3 + 'apps/viewer/editor.html') + localFlag;
Data.apps.eyewear = (local ? local + ':3000/' : s3 + 'apps/eyewear/index.html') + localFlag;
Data.source = new TempData( local ? local + ':2020/example/storage.ignore/' : s3 );
