import { IProductData } from '@designhubz-widget/data';
import { _Product } from './_Product';
import { _3DProduct } from './_3DProduct';
import { _EyewearProduct } from './_EyewearProduct';

let _productsCache: Record<string, _Product<any>> = {};
export function ProductFromCache<T>(workspaceKey: string, productKey: string): _Product<T> | undefined
{
    return _productsCache[workspaceKey + productKey];
}

let _variationsCache: Record<string, _Product<any>[]> = {};
function VariationsFromCache<T>(workspaceKey: string, productKey: string): _Product<T>[] | undefined
{
    return _variationsCache[workspaceKey + productKey];
}

export function ProductFromData<T extends Record<string, string>>(data: IProductData, props?: T)
{
    let product = ProductFromCache<T>(data.workspaceKey, data.productKey);
    if(product !== undefined) return product;

    let variations = VariationsFromCache<T>(data.workspaceKey, data.productKey);
    if(variations === undefined)
    {
        variations = [];
        data.variations.forEach( variation => {
            const vData = variation as IProductData;
            _variationsCache[vData.workspaceKey + vData.productKey] = variations!;
        });
    }

    if(props !== undefined)
    {
        let missingProps: string[] = [];
        for(const key in props)
        {
            const header = props[key];
            if(header in data.properties)
            {
                if(key in data.properties) console.warn(`Custom property accessor '${key}' will override existing property.`);
                Object.defineProperty(data.properties, key, {
                    get: () => data.properties[header]
                });
            }
            else
            {
                missingProps.push(header);
            }
        }

        if(missingProps.length > 0)
        {
            const plr = missingProps.length > 1;
            console.warn(`Custom propert${plr ? 'ies' : 'y'}: '${missingProps.join(`', '`)}' ${plr ? 'are' : 'is'} missing from '${data.productKey}'`);
        }
    }

    const productArgs: [string, string, T, _Product<T>[]] = [
        data.workspaceKey,
        data.productKey,
        data.properties,
        variations
    ];

    switch(data.productType)
    {
        case '3D':
            product = new _3DProduct<T>(...productArgs);
        break;
        case 'Eyewear':
            product = new _EyewearProduct<T>(...productArgs);
        break;
        case 'Makeup':
            throw 'Not implemented';
        default:
            throw 'Not implemented';
    }

    variations.push(product);
    _productsCache[data.workspaceKey + data.productKey] = product;
    return product;
}
