
export const DESIGNHUBZ_API_TYPE = 'designhubz-api';

export const enum WidgetEvent {
    Init = 'Init',
    SnapshotRequest = 'SnapshotRequest',
    Save = 'Save',
    Debug = 'Debug'
}

export interface IWidgetMessageData
{
    type: string;
    id: string;
    event: WidgetEvent;
    value: any;
}

export type TWidgetMessage = { data: IWidgetMessageData };
