import { ISnapshot } from '@designhubz-widget/api';

type TImageSource = (CanvasImageSource & {width: number, height: number}) | ImageData;
let _canvas: HTMLCanvasElement;

function DrawToCanvas(source: TImageSource, flip: boolean)
{
    if(_canvas === undefined) _canvas = document.createElement('canvas');

	_canvas.width = source.width;
	_canvas.height = source.height;

    const context = _canvas.getContext('2d')!;
    context.resetTransform();
    if(flip)
    {
        context.translate(0, _canvas.height);
        context.scale(1, -1);
    }

    if(source instanceof ImageData) context.putImageData(source, 0, 0);
    else context.drawImage(source, 0, 0, source.width, source.height);
    return _canvas;
}

export class Snapshot implements ISnapshot
{
    constructor(public readonly imageData: ImageData)
    {}

    public getDataURL(type: 'jpeg' | 'png', quality?: number)
    {
        const canvas =  DrawToCanvas(this.imageData, false);
        return canvas.toDataURL(type === 'jpeg' ? 'image/jpeg' : 'image/png', quality)
    }

    public async getBlobAsync(type: 'jpeg' | 'png', quality?: number)
    {
        const canvas =  DrawToCanvas(this.imageData, false);
        const blob = await new Promise<Blob>( (resolve, reject) => 
            canvas.toBlob( blob => {
                if(blob === null) reject('Failed to generate Blob');
                else resolve(blob);
            }, type === 'jpeg' ? 'image/jpeg' : 'image/png', quality)
        );

        return blob;
    }
}
