console.log('@designhubz-widget/data');

import { IDataSource, IProductData } from './DataSource';

export interface ISkinData
{
    readonly descriptorURL: string;
}

export interface ISceneData
{
    readonly descriptorURL: string;
}

export * from './DataSource';

export namespace Data
{
    export let source: IDataSource;

    export const apps = {
        viewer: '',
        eyewear: '',
        editor: ''
    };

    export async function GetScene(workspaceKey: string, productKey: string)
    {
        const response = await source.GetSceneResponse(workspaceKey, productKey);
        if(response.status !== 'success' || typeof response.data[0] !== 'object')
            throw new Error(`Error: GetScene(${workspaceKey}: string, ${productKey}: string)`);
        
        return {
            name: response.data[0].name,
            publicFolderUrl: response.data[0].publicFolderUrl,
            sceneData: response.data[0].sceneData as ISceneData
        };
    }

    export async function GetSkin(workspaceKey: string)
    {
        const response = await source.GetSkinResponse(workspaceKey);
        if(response.status !== 'success' || typeof response.data[0] !== 'object')
            throw new Error(`Error: GetSkin(${workspaceKey}: string)`);

        return {
            publicFolderUrl: response.data[0].publicFolderUrl,
            skinData: response.data[0].skinData as ISkinData
        };
    }

    export async function FetchProduct(workspaceKey: string, productKey: string)
    {
        const response = await source.FetchProductResponse(workspaceKey, productKey, true);
        if(response.status !== 'success' || typeof response.data[0] !== 'object')
            throw new Error(`Error: GetProductData(${workspaceKey}: string, ${productKey}: string)`);

        return {
            productData: response.data[0].productData
        };
    }

    export async function FetchProducts(workspaceKey: string, productKeys: string[])
    {
        const response = await source.FetchProductsResponse(workspaceKey, productKeys, true);
        if(response.status !== 'success' || typeof response.data[0] !== 'object')
            throw new Error(`Error: GetProductData(${workspaceKey}: string, [${productKeys.join(',')}]: string[])`);

        const map: Record<string, {productData: IProductData}> = {};
        response.data.forEach( item => map[item.productData.productKey] = item );
        return map;
    }
}
